





















import {Component, Inject, Vue} from "vue-property-decorator";
import News from "@/js/models/News";

@Component({
  data: () => ({
    news: {name: ''}
  }),
  metaInfo() {
    return {
      // @ts-ignore
      title: this.news.header,
      meta: [
        {
          vmid: 'description',
          name: 'description',
          // @ts-ignore
          content: `${this.news.short_description}`,
        }
      ]
    }
  }

})
export default class ViewNews extends Vue{
  news: News| null = null
  @Inject('imageBasePath') imageBasePath!: string

  created (): void {
    News.get({ id: Number(this.$route.params.id) })
        .then((response) => {
          this.news = response.data
    })
  }
}
